import { request } from 'utils';

const prefix = '/api/finance';


export const getBills = ({page,pageSize=10,orderBy,isPagingEnabled=true} = {}) =>
  request({
    prefix,
    url: '/ReceiptConfig',
    method: 'GET',
    params: {page,pageSize,orderBy,isPagingEnabled},
    data: {}
  });

export const searchBill = ({
  fiscalYear,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: `/ReceiptConfig/GetReceipConfigByFiscalYear`,
    method: 'GET',
    params: {
      fiscalYear,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });



export const getBillById = ({id} = {}) =>
  request({
    prefix,
    url: `/ReceiptConfig/${id}`,
    method: 'GET',
    params: {},
    data: {}
  });

export const createBill = ({
  label,
  fiscalYear,
  invoiceTemplate,
  invoiceCode,
  previousRemainFrom,
  previousRemainTo,
  publishFrom,
  publishTo,
  isActive,
  isEnrolled
  } = {}) =>
  request({
    prefix,
    url: '/ReceiptConfig',
    method: 'POST',
    params: {},
    data: {
      label,
      fiscalYear,
      invoiceTemplate,
      invoiceCode,
      previousRemainFrom,
      previousRemainTo,
      publishFrom,
      publishTo,
      isActive,
      isEnrolled
    }
  });

export const updateBill = ({
  id,
  label,
  fiscalYear,
  invoiceTemplate,
  invoiceCode,
  previousRemainFrom,
  previousRemainTo,
  publishFrom,
  publishTo,
  isActive,
  isEnrolled
} = {}) =>
  request({
  prefix,
  url: `/ReceiptConfig/${id}`,
  method: 'PUT',
  params: {},
  data: {
    id,
    label,
    fiscalYear,
    invoiceTemplate,
    invoiceCode,
    previousRemainFrom,
    previousRemainTo,
    publishFrom,
    publishTo,
    isActive,
    isEnrolled
  }
  });

export const deleteBill = (id) =>
  request({
  prefix,
  url:`/ReceiptConfig/${id}`,
  method: 'DELETE',
  params: {},
  data: {}
  });


